@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #EADFCA;
}


.expenses {
    padding: 1rem;
    background-color: #FCDB87;
    width: 33rem;
    max-width: 95%;
    border: 2px solid black;
    margin-left: 1rem;
    box-shadow: 5px 5px 0px ;
}

.expenses_bg {
    padding: 1px;
    border-radius: 1rem;
    margin: 1rem;
    border: 2px solid black;
    background-color: #BEB197;
    
}

.expenses_bg_two {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
}
.card {
    border-radius: 1rem;
    /* box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25 ); */
}
.expenses-filter {
    padding: 0 1rem;
  }
  
  .expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 1rem 0;
  }
  
  .expenses-filter label {
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .expenses-filter select {
    font: inherit;
    padding: 0.5rem 3rem;
    font-weight: bold;
    border-radius: 6px;
  }

  button {
    background-color: #BEB197;
    border-right: 2px solid black;
    border-radius: 8px;
    -webkit-appearance: none;
    appearance: none;
    font-family: inherit;
    font-weight: 800;

    margin: 0px 5px auto;
    padding: 5px 15px;

    text-align: center;
    text-decoration: none;
    display: inline-block; /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
  }

  button:hover {
    background-color: white;
  }

  .line {
    border: 1px solid black;

  }

.expenses-list {
    list-style: none;
    padding: 0;
}

.expenses-list__fallback {
    font-size: 1rem;
    text-align: center;
}
.expense-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
  }
  
  .expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    flex-flow: column-reverse;
    align-items: flex-end;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-bg {
    background-color: #ffffff;
    border-radius: 12px;
    flex: 6 1;
    height: 5.5rem;
    margin-left: 1rem ;
    border: 2px solid black;
    align-items: center;
  }
  
  .expense-item h2 {
    font-size: 1rem;
    margin: 1rem;
    margin-left: 1.5rem;
    display: flex;
    flex: 1 1;
    align-items: center;
    line-height: 50px;
  }
  
  .expense-item__price {
    font-size: 1rem;
    font-weight: bold;
    padding: 0rem 1rem;
    background-color: #FFBC6C;
    /* padding: 0.5rem; */
    border-radius: 12px;
    border: 2px solid black;
  }

