.expense-date {
    display: flex;  
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    background-color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
  }
  
  .expense-date__month {
    font-size: 0.75rem;
    font-weight: bold;
  }
  
  .expense-date__year {
    font-size: 0.75rem;
  }
  
  .expense-date__day {
    font-size: 1.5rem;
    font-weight: bold;
  }

.chart-bar {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    flex: 0 0 100%;
  }
  
  .chart-bar__inner {  
    display: flex;
    height: 1.1rem;
    width: 100%;
    flex: 1 1;
    border-radius: 12px;
    margin: 0.5rem;
    background-color: #ffffff;
    border: 2px solid black;
    overflow: hidden;
  }
  
  .chart-bar__fill {
    background-color: #707f4a;
    width: 100%;
    transition: all 0.3s ease-in ;

  }
  
  .chart-bar__label {
    font-weight: bold;
    font-size: 0.5rem;
    text-align: center;
  }
.chart {
    padding: 1rem;
    margin: 1rem;

    border-radius: 12px;
    background-color: #FCDB87;
    text-align: center;
    display: flex;

    width: 10rem;
    height: 100%;
    border: 2px solid black;
    flex-wrap: wrap;

    box-shadow: 5px 5px 0px ;

  }

.new-expense {
  width: 15rem;
    background-color: #BEB197;
    padding: 1rem;
    height:100%;
    margin: 1rem 0rem 1rem 1rem;
    border-radius: 12px;
    text-align: center;
    border: 2px solid black;
  }
  
  .new-expense button {
    cursor: pointer;
    font: inherit;
    font-weight: 800;
    font-size: 2px;
    color: #000;

    width: 6rem;
    padding: 0.5rem;
    margin: 0.1rem;
    border: 2px solid black;
    background-color: #BBE393;
    border-radius: 6px;
  }
  
  .new-expense button:hover,
  .new-expense button:active {
    border: 2px solid black;
    background-color: #FCDB87;
  }
  
  .new-expense button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-expense button.alternative:hover,
  .new-expense button.alternative:active {
    border: 2px solid black;
    background-color: #FCDB87;
  }

.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    font-size: 0.7rem;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 2px solid rgb(0, 0, 0);
    width: 12.5rem;
    max-width: 100%;
    height: 2.5rem;
  }
  
  .new-expense__actions {
    margin: 0;
  }
.app_outer {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;

    display: flex;
}

.app_inner {
    flex: 1 1;
    display: flex;
    position: absolute;
    margin: auto;
    margin: 0px auto;
    vertical-align: middle;
    padding: 1px;
    width: 1040px;
    border: 2px solid black;
    border-radius: 1rem;
}
